<template>
<v-simple-table dense v-if="data" style="overflow-y: auto">
  <thead>
    <tr>
      <th v-for="(item, key) in data.headers" :key="key" v-text="item"/>
      <th v-if="$slots.actions">Действия</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="(row, key) in data.data" :key="key">
      <td v-for="(item, key) in data.headers" :key="key" v-text="row[key]"/>
      <td><slot name="actions" v-bind="{ item: row }"/></td>
    </tr>
  </tbody>
</v-simple-table>
<v-container fill-height v-else>
  <v-row justify="center">
    <v-progress-circular
        indeterminate
        color="primary"
    ></v-progress-circular>
  </v-row>
</v-container>
</template>

<style scoped>
th, td {
  font-size: 0.8rem !important;
}
</style>

<script>
export default {
  props: ['func', 'filter'],

  watch: {
    data() {
      this.$emit('update', { ...this.data });
    }
  },

  asyncComputed: {
    data() {
      return this.api.findFiltered(this.func, this.filter);
    }
  }
};
</script>
